<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-cl-profile"></i></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button type="success" round  @click="addRole"></el-button>
        <el-table
          :data='RoleData'
          tooltip-effect="dark"
          style="width: 100%"
        >
          <el-table-column
            prop="name"
            label=""
            width="200">
          </el-table-column>
          <el-table-column prop='ctime' label=''  width="200">
            <template slot-scope="scope">
              <div slot="reference" class="name-wrapper" style="text-align: left; color: rgb(0,0,0)">
                <span>{{scope.row.ctime | parseDate}}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="desc"
            label=""
            width="300">
          </el-table-column>
          <el-table-column label=""  width="180" aligin="center">
            <template slot-scope="scope">
              <el-button type="primary"    icon="el-icon-edit" circle></el-button>
              <el-button type="danger"  @click="handleDelete(scope.row)"  icon="el-icon-delete" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>

  import { getIRoles,deleteRole} from '../../api/api'
    export default {
        name: "Roles",
      created() {
        this.getRoleInfo();
      },
      watch: {
        // 
        "$route": "getRoleInfo"
      },
      data(){
        return{
          RoleData: [],
        }
      },
      methods:{
        //  
        getRoleInfo(){
          getIRoles()
            .then(res=>{
              console.log(res)
              this.RoleData = res.data;
              this.$store.commit("ROLE",res.data)

            }).catch(function (error) {
            console.log(error)
          })
        },
        //
        handleDelete(row){
          this.$confirm("").then(()=>{
            deleteRole(row.id).then((res)=>{
              this.$message({
                message: '',
                type: 'success'
              })
              this.getRoleInfo()
            })

          }).catch(error => {
            this.$message.info('')
            console.log(error)
          })
        },


        addRole(){
          this.$router.push('/role/add')
        }
      }
    }
</script>

<style scoped>

</style>



